.container {
      margin: 0 auto;
      width: 1170px;
    }
    .Nav {
      width: 100%;
      float: left;
    }
    ul {
      width: 100%;
      float: left;
    }
    li {
      display: inline-block;
      margin: 4px 5px 3px 46px;
    }
    button {
      padding: 12px 8px;
      outline: none;
      border: navajowhite;
      border-bottom: 2px solid;
      font-family: sans-serif;
      font-weight: bold;
      cursor: pointer;
      transition: 0.8s;
    }
    button:hover{
    color: #fff;
    background: #000;
    }